@charset "UTF-8";
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy/Gilroy-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy/Gilroy-Regular.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy/Gilroy-Bold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
p {
  margin-top: 0;
}

#wpcontent {
  padding-left: 0;
}

.rft-plugin-tm .nav-container {
  background-color: white;
}
.rft-plugin-tm nav {
  display: flex;
  max-width: 750px;
  padding: 12px 30px;
}
.rft-plugin-tm nav a {
  font-size: 15px;
  text-align: center;
  color: #000000;
  cursor: default;
  margin: 0 3px;
  padding: 8.5px 10px 8.5px 30px !important;
  min-width: 180px;
  float: left;
  position: relative;
  background-color: #d9e3f7;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: background-color 0.2s ease;
  text-decoration: none;
  font-weight: 600;
}
.rft-plugin-tm nav a:hover {
  cursor: pointer;
}
.rft-plugin-tm nav a.active {
  color: #fff;
  background-color: #23468c;
}
.rft-plugin-tm nav a.active:after {
  border-left: 17px solid #23468c;
}
.rft-plugin-tm nav a:after, .rft-plugin-tm nav a:before {
  content: " ";
  position: absolute;
  top: 0;
  right: -17px;
  width: 0;
  height: 0;
  border-top: 19px solid transparent;
  border-bottom: 17px solid transparent;
  border-left: 17px solid #d9e3f7;
  z-index: 2;
  transition: border-color 0.2s ease;
}
.rft-plugin-tm nav a:before {
  right: auto;
  left: 0;
  border-left: 17px solid #fff;
  z-index: 0;
}
.rft-plugin-tm nav a:first-child:before {
  border: none;
}
.rft-plugin-tm nav a:first-child {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.rft-plugin-tm nav > * {
  flex-basis: 100%;
}
.rft-plugin-tm .container {
  max-width: 750px;
  margin: 30px;
  padding: 30px;
  background-color: white;
  border-radius: 8px;
}
.rft-plugin-tm .container .content .additional-information p {
  font-size: 15px;
  font-weight: 600;
}
.rft-plugin-tm .container .content .message-field {
  margin-top: 10px;
}
.rft-plugin-tm .container .content .message-field.error-field {
  color: red;
  font-size: 15px;
  font-weight: 600;
}
.rft-plugin-tm .container .content .message-field.apply-field {
  color: green;
  font-size: 15px;
  font-weight: 600;
}
.rft-plugin-tm .container .content .type-integration {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 30px 0 24px 0;
}
.rft-plugin-tm .container .content .type-integration .integration {
  display: flex;
}
.rft-plugin-tm .container .content .type-integration .integration input {
  margin-top: 0;
}
.rft-plugin-tm .container .content .type-integration .integration label {
  font-family: Gilroy;
  font-size: 14px;
  font-weight: 600;
}
.rft-plugin-tm .container .content .type-integration .integration label span {
  display: block;
  font-family: Gilroy;
  font-size: 13px;
  font-weight: 500;
  margin-top: 6px;
}
.rft-plugin-tm .container .content .plugin-integration {
  padding-top: 24px;
  border-top: 1px solid #EFEFEF;
}
.rft-plugin-tm .container .content .plugin-integration .single-integration {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  margin-bottom: 24px;
}
.rft-plugin-tm .container .content .plugin-integration .single-integration input {
  margin-top: 0;
}
.rft-plugin-tm .container .content .plugin-integration .single-integration label {
  font-family: Gilroy;
  font-size: 14px;
  font-weight: 600;
}
.rft-plugin-tm .container .content .plugin-integration .single-integration label span {
  font-family: Gilroy;
  font-size: 13px;
  font-weight: 500;
  margin-top: 6px;
  display: block;
}
.rft-plugin-tm .container .content .select-global-form {
  position: relative;
}
.rft-plugin-tm .container .content .select-global-form label {
  font-family: Gilroy;
  font-size: 11px;
  font-weight: 500;
  background: #ffffff;
  position: absolute;
  top: -10px;
  left: 10px;
}
.rft-plugin-tm .container .content .select-global-form select {
  font-family: Gilroy;
  width: 100%;
  max-width: 100%;
  padding: 8px 16px;
  border: 1px solid #E9E9E9;
  border-radius: 8px;
  font-size: 15px;
  appearance: none;
  -webkit-appearance: none;
  font-weight: 500;
  background-image: linear-gradient(45deg, transparent 50%, grey 50%), linear-gradient(135deg, grey 50%, transparent 50%);
  background-position: calc(100% - 19.66px) 48%, calc(100% - 15px) 48%, 100% 0;
  background-size: 5px 5px, 5px 5px, 2.5em 2.5em;
  background-repeat: no-repeat;
}
.rft-plugin-tm .container .content .select-global-form select:hover {
  background-image: linear-gradient(45deg, grey 50%, transparent 50%), linear-gradient(135deg, transparent 50%, grey 50%);
  background-position: calc(100% - 15.66px) 48%, calc(100% - 20px) 48%, 100% 0;
  background-size: 5px 5px, 5px 5px, 2.5em 2.5em;
  background-repeat: no-repeat;
  outline: 0;
}
.rft-plugin-tm .container .content .select-global-form select:focus {
  border: 1px solid #FCBE66;
  box-shadow: unset;
}
.rft-plugin-tm .container .content .global-form-settings {
  border-top: 1px solid #EFEFEF;
  margin-top: 24px;
  padding-top: 24px;
}
.rft-plugin-tm .container .content .global-form-settings .form-message, .rft-plugin-tm .container .content .global-form-settings .form-message-after-successful {
  position: relative;
  margin-bottom: 24px;
}
.rft-plugin-tm .container .content .global-form-settings .form-message input, .rft-plugin-tm .container .content .global-form-settings .form-message-after-successful input {
  font-family: Gilroy;
  width: 100%;
  max-width: 100%;
  padding: 8px 16px;
  border: 1px solid #E9E9E9;
  border-radius: 8px;
  font-size: 15px;
  font-weight: 500;
}
.rft-plugin-tm .container .content .global-form-settings .form-message input:focus, .rft-plugin-tm .container .content .global-form-settings .form-message-after-successful input:focus {
  border: 1px solid #FCBE66;
  box-shadow: unset;
}
.rft-plugin-tm .container .content .global-form-settings .form-message label, .rft-plugin-tm .container .content .global-form-settings .form-message-after-successful label {
  font-family: Gilroy;
  font-size: 11px;
  font-weight: 500;
  background: #ffffff;
  position: absolute;
  top: -10px;
  left: 10px;
  padding: 0 3px;
}
.rft-plugin-tm .container .content .global-form-settings .modify-inputs {
  margin-bottom: 24px;
}
.rft-plugin-tm .container .content .global-form-settings .modify-inputs .rft-rename-group input, .rft-plugin-tm .container .content .global-form-settings .modify-inputs .rft-rename-group select, .rft-plugin-tm .container .content .global-form-settings .modify-inputs .rft-rename-group button {
  border: 1px solid #E9E9E9;
  border-radius: 8px;
  padding: 8px 16px;
}
.rft-plugin-tm .container .content .global-form-settings .modify-inputs .rft-rename-group button {
  height: 45px;
  background: unset;
  color: #1F64A5;
  border: unset;
  font-weight: 600;
  cursor: pointer;
}
.rft-plugin-tm .container .content .global-form-settings .modify-inputs .rft-rename-group button.rft-bold-format, .rft-plugin-tm .container .content .global-form-settings .modify-inputs .rft-rename-group button.rft-italic-format {
  border: 1px solid #1F64A5;
}
.rft-plugin-tm .footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid #E9E9E9;
}
.rft-plugin-tm .btn {
  border-radius: 8px;
  padding: 15px 16px;
  font-size: 15px;
  font-weight: 600;
  text-decoration: none;
}
.rft-plugin-tm .btn.rivo-rate {
  border: 1px solid #1B2023;
  color: #1B2023;
  background-size: 200% 100%;
  background-position: right;
  background-repeat: none;
}
.rft-plugin-tm .btn.rivo-rate:hover {
  color: #ffffff;
  background-position: left;
  background-image: linear-gradient(to right, #1D81AE, #2D93BF, #1E9C9C);
}
.rft-plugin-tm .btn.rivo-start {
  color: #ffffff;
  background-image: linear-gradient(to right, #1D81AE, #2D93BF, #1E9C9C);
}

input.input-error,
input.input-error:focus {
  outline: 1px solid red;
  box-shadow: unset;
}

.token-field {
  display: flex;
