  padding: 15px 16px;
  font-family: Gilroy;
  font-size: 15px;
  font-weight: 500;
  color: #000000;
}

#tm_bot_token::placeholder {
  color: rgba(27, 32, 35, 0.5019607843);
}

.aply-tocken-button {
  height: 48px;
  width: 116px;
  border-radius: 8px;
  border: 1px solid #1F64A5;
  padding: 15px 16px 15px 16px;
  font-family: Gilroy;
  font-size: 15px;
  font-weight: 600;
}

.chats-list {
  margin-top: 24px;
  border-top: 1px solid #EFEFEF;
  padding-top: 24px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.chats-list p {
  font-family: Gilroy;
  font-size: 15px;
  font-weight: 600;
  margin: 0;
}

.isDisabled {
  color: currentColor;
  cursor: not-allowed;
  opacity: 0.5;
  text-decoration: none;
}

.rft-wrapper {
  margin-right: 7vw;
}
.rft-menu-tabs-wrapper {
  display: flex;
  flex-direction: row;
}
.rft-menu-tabs-list {
  display: flex;
  flex-direction: column;
  padding: 0px;
  margin-top: 20px;
  flex: 0 0 auto;
}
.rft-menu-tabs-list [role=tab] a {
  text-decoration: none;
  color: #1d2327;
}
.rft-menu-tabs-list [role=tab].child {
  margin-left: 20px;
}
.rft-menu-tabs-list [role=tab].child a {
  color: rgba(29, 35, 39, 0.7333333333);
}
.rft-menu-tabs-list [role=tab][aria-selected=true] {
  background: #2271b1;
}
.rft-menu-tabs-list [role=tab][aria-selected=true] a {
  color: #fff;
}
.rft-tab-content {
  flex: 1 1 auto;
  max-width: 1150px;
  border-left: 2px solid #2271b1;
}
.rft-tab-content [data-tabs] {
  margin: 0;
  padding-bottom: 25px;
  border-bottom: 1px solid #d7d7d9;
}
.rft-tab-content [data-tabs] > [role=tab] {
  background: #f7f7f7;
  margin-right: 10px;
}
.rft-tab-content [data-tabs] > [role=tab] a {
  color: #1d2327;
  text-decoration: none;
}
.rft-tab-content [data-tabs] > [role=tab][aria-selected=true] {
  background: #2271b1;
}
.rft-tab-content [data-tabs] > [role=tab][aria-selected=true] a {
  color: #fff;
}
.rft-tab-content .form-group {
  padding-top: 12px;
  padding-bottom: 34px;
}
.rft-form {
  margin-bottom: 40px;
  margin-right: 5px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.4588235294);
  border-radius: 6px;
  min-height: 40vh;
}
.rft-form h3, .rft-form h4 {
  margin: 0;
}
.rft-input-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 8px;
  flex-wrap: wrap;
}
.rft-input-group.textarea-input-group {
  margin-bottom: 15px;
}
.rft-input-group.textarea-input-group label {
  width: 80px;
}
.rft-input-group label {
  font-weight: 500;
  min-width: 80px;
  margin-right: 7px;
}
.rft-input-group p {
  width: 100%;
}
.rft-input-group input[type=text] {
  min-width: 50%;
  margin-right: 6px;
}
.rft-input-group textarea {
  min-width: 50%;
  margin-right: 6px;
}
.rft-input-group button.button.button-secondary {
  margin-right: 6px;
}
.rft-rename-group {
  display: flex;
  margin-bottom: 24px;
  flex-wrap: nowrap;
  align-items: center;
  gap: 10px;
}
.rft-rename-group input, .rft-rename-group select {
  margin-left: 6px;
}
.rft-rename-group .id {
  padding: 4px;
  width: 18px;
  text-align: left;
}
.rft-rename-group .text-format {
  margin-left: 6px;
  display: contents;
}
.rft-rename-group .text-format button {
  width: 28px;
  height: 28px;
  margin-left: 6px;
  font-size: 14px;
  text-align: center;
  padding: 5px;
  font-family: serif;
  display: inline-block;
  border: 1px solid #d7d7d9;
  border-radius: 4px;
  cursor: pointer;
}
.rft-rename-group .text-format button.selected {
  background: #2271b1;
  color: #fff;
  border: 1px solid transparent;
}
.rft-submit-wrap {
  margin-top: 20px;
}

#tm-bot-notices {
  display: none;
  padding: 10px;
